@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.button {
  min-width: max-content;
  padding: 14px 28px;
  font-family: "Roboto Condensed","Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
}

.contacts-link {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  list-style: none;
}

.contacts-link__item {
  min-width: max-content;
  font-family: "PT Mono", "Nimbus Mono L", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
}

.contacts-link__item:not(:last-child) {
  margin-right: 88px;
}

.contacts-link__item a {
  display: flex;
  align-items: center;
  color: inherit;
  cursor: pointer;
}

.contacts-link__item a svg {
  stroke: #00b2ff;
}

.contacts-link__item a:visited {
  color: inherit;
}

.contacts-link__item a:hover {
  color: #00b2ff;
}

.contacts-link__item a:hover svg {
  stroke: #ffffff;
}

.contacts-link__item a:active {
  opacity: 0.5;
}

.contacts-link__item--mail svg {
  width: 19px;
  height: 14px;
  margin-right: 20px;
}

.contacts-link__item--telephone svg {
  width: 18px;
  height: 18px;
  margin-right: 16px;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0 auto;
}

.page-body--opened-menu {
  overflow: hidden;
}

.page-header {
  padding-top: 33px;
  background-color: #1b2d37;
}

.page-header__logo {
  margin-right: 30px;
}

.page-header__logo-image {
  width: 52px;
  height: 52px;
  margin-right: 10px;
}

.page-header__logo-title {
  width: 134px;
  height: 45px;
}

.page-header__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 1470px;
  margin: 0 auto;
}

.page-header__contacts {
  flex-grow: 1;
  margin-top: 0;
  margin-right: 50px;
  margin-bottom: 0;
  padding-left: 0;
}

@media (max-width: 1023px) {
  .contacts-link {
    align-items: center;
  }

  .contacts-link__item:not(:last-child) {
    margin-right: 33px;
  }

  .contacts-link__item span {
    display: none;
  }

  .contacts-link__item--mail svg {
    width: 25px;
    height: 19px;
    margin-right: 0;
  }

  .contacts-link__item--telephone svg {
    width: 24px;
    height: 24px;
    margin-right: 0;
  }

  .page-header {
    padding-top: 20px;
  }

  .page-header__link {
    padding-left: 25px;
    padding-right: 25px;
  }

  .page-header__contacts {
    margin-right: 0;
  }

  .page-header__order-call {
    display: none;
  }
}

@media (max-width: 767px) {
  .contacts-link__item:not(:last-child) {
    margin-right: 23px;
  }

  .page-header {
    padding-top: 17px;
  }

  .page-header__logo-image {
    width: 38px;
    height: 38px;
  }

  .page-header__logo-title {
    display: none;
  }

  .page-header__link {
    padding-left: 17px;
    padding-right: 17px;
  }
}